<template>
    <div id="mobile-nav">
        <b-button id="sidebar-main-close" v-b-toggle.sidebar-main class="navbar-toggler">
            <span class="navbar-toggler-bar top-bar"></span>
            <span class="navbar-toggler-bar middle-bar"></span>
            <span class="navbar-toggler-bar bottom-bar"></span>
        </b-button>
        <img src="@/assets/img/logo-stirpe-negro.svg" class="logo" alt="Logo tienda Stirpe"> 
        <div class="d-inline-block float-right my-lg-0 ml-2">
            <shortcut-menu />
        </div>        
        <b-sidebar id="sidebar-main" title="Menu" no-header width="100%" shadow>
            <div :key="index" v-for="(item, index) in menu">
                <mobile-menu-item :menuItem="item" />
            </div>
            <div id="mobile-nav-menu-small">
                <b-button squared block :to="{ name: 'shops' }" class="m-0">Encuentra una tienda</b-button>
                <b-button squared block href="#" class="m-0">Visita <img src="@/assets/icons/logo-derek.svg" style="margin-top: -5px;" alt="Logo tienda Stirpe"></b-button>
                <b-button squared block :to="{ name: 'helpCenter' }" class="m-0">Centro de ayuda</b-button>
                <b-button squared block :to="{ name: 'tracert' }" class="m-0">Rastrear pedido</b-button>
            </div>
        </b-sidebar>
    </div> 
</template>

<script>
import MobileMenuItem from '@/components/menu/MobileMenuItem'
import ShortcutMenu from '@/components/menu/ShortcutMenu'

export default {
    name: 'MobileMenu',   
    components: {
        MobileMenuItem,
        ShortcutMenu
    },
    props: {
        menu:{
            type: Array,
            default: () => [],
            required: true
        }
    }
}
</script>

<style>
@media (max-width:768px) {
    body{
        padding-top: 65px;
    }
}
#mobile-nav{
    padding: 15px .5rem;
    display: block;
}
#sidebar-main{
    top: 4rem;
    z-index: 100;
}
#sidebar-main .b-sidebar-body{
    padding-bottom: 4rem;
}
#sidebar-main .b-sidebar-body{
    background: #fff;
}
#sidebar-main .mobile-nav-btn, #sidebar-main a.btn{
    background: #fff;
    color: #000 !important;
    padding: 20px 40px;
    text-align: left;
    border: none;
}
#sidebar-main .mobile-nav-btn{
    border-top: 1px solid #e9e9e9;
}
#sidebar-main .btn .b-icon.bi{
    float: right;
}
#mobile-nav #mobile-nav-menu-small{
    border-top: 1px solid #e9e9e9;
    padding-top: 8rem;
}
#mobile-nav #mobile-nav-menu-small .btn{
    padding: 10px 40px;
}
/*   Boton principal   */
#mobile-nav .navbar-toggler {
    width: 48px;
    height: 35px;
    outline: 0;
    cursor: pointer;
    padding: .25rem .75rem;
    font-size: 1.25rem;
    line-height: 1;
    background-color: transparent;
    border: 1px solid transparent;
    border-radius: .25rem;
}
#mobile-nav .navbar-toggler:focus {
    box-shadow: none;
}
#mobile-nav .navbar-toggler .navbar-toggler-bar {
    display: block;
    position: relative;
    width: 22px;
    height: 3px;
    border-radius: 1px;
    background: #809AB8;
}
#mobile-nav .navbar-toggler-bar+.navbar-toggler-bar {
    margin-top: 6px;
}
#mobile-nav .bottom-bar, #mobile-nav .middle-bar, #mobile-nav .top-bar {
    -webkit-transition: all .2s;
    transition: all .2s;
}
#mobile-nav .navbar-toggler-bar.middle-bar {
    -webkit-transition: width .1s linear;
    transition: width .1s linear;
}
#mobile-nav .top-bar {
    -webkit-transform: rotate(0);
    transform: rotate(0);
}
#mobile-nav .bottom-bar {
    -webkit-transform: rotate(0);
    transform: rotate(0);
}
#mobile-nav .not-collapsed .navbar-toggler-bar{
    width: 26px;
}
#mobile-nav .not-collapsed .bottom-bar {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    -webkit-transform-origin: 0;
    transform-origin: 0;
}
#mobile-nav .not-collapsed .top-bar {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    -webkit-transform-origin: 0;
    transform-origin: 0;
}
#mobile-nav .not-collapsed .middle-bar {
    opacity: 0;
    width: 0;
}
</style>