import Vue from 'vue'
import VueRouter from 'vue-router'

import Error from '@/views/Error'


Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import(/* webpackChunkName: "Home" */ '@/views/home/Home.vue')
  },
  {
    path: '/sobre-nosotros',
    name: 'About',
    component: () => import(/* webpackChunkName: "About" */ '@/views/About.vue')
  },
  {
    path: '/:gender',
    name: 'HomeGender',
    component: () => import(/* webpackChunkName: "HomeGender" */ '@/views/home/HomeGender.vue')
  },

  {
      path: '*',
      name: 'Error',
      component: Error
  }
]

const router = new VueRouter({
  routes,
  mode: 'history'
})

export default router
