<template>
    <div>
        <template v-if="menuItem.subMenu">
            <b-button  squared block v-b-toggle="id" class="mobile-nav-btn"> {{ menuItem.title }} <b-icon icon="chevron-right" variant="dark"></b-icon></b-button>
            <b-sidebar :id=id width="100%" no-header right>
                <template v-slot:default="{ hide }">
                <b-row class="text-center">
                    <b-col class="p-0"><b-button @click="hide" block class="h-100 mobile-nav-btn" > <b-icon icon="chevron-left" variant="dark"></b-icon></b-button></b-col>
                    <b-col class="p-0" cols="8"><b-button class="pl-5 mobile-nav-btn" squared block @click="goTo({ name: menuItem.routerName, params: menuItem.params })">{{ menuItem.title }}</b-button></b-col>
                </b-row>
                <div :key="index" v-for="(item, index) in menuItem.subMenu">
                    <mobile-menu-item :menuItem="item"></mobile-menu-item>
                </div>
                </template>
            </b-sidebar>
        </template>
        <b-button squared v-else block class="mobile-nav-btn" @click="goTo({ name: menuItem.routerName, params: menuItem.params })">{{ menuItem.title }}</b-button>
    </div>
</template>

<script>
export default {
    name: 'MobileMenuItem',
    data() {
        return {
            id: this.menuItem.routerName + this.menuItem.id
        }
    },
    props: {
        menuItem:{
            type: Object, //{title: 'Hombre', routerName: 'Home', params: {gender:'hombre'}, subMenu: []}
            required: true
        }
    },
    methods:{
        goTo(params){
            this.$router.push(params).catch(err => {
                console.log(err)
                document.getElementById('sidebar-main-close').click();
            })
        }
    }
}
</script>
