<template>
    <div id="search-input" class="d-inline-block position-relative flex-fill">
        <md-autocomplete 
            :model="query" 
            :value="query"
            :md-options="suggestions" 
            md-input-id="search-autocomplete"
            md-layout="box" 
            md-input-placeholder="Busca por marcas, prendas o referencia"
            md-dense
            :md-open-on-focus="false"
            @md-opened="resize"
            @md-selected="search">
        </md-autocomplete>
        <b-img-lazy :blank-color="'#bbb'" loading="lazy" src="@/assets/icons/search.svg" fluid id="search-icon" alt="Busca ropa, zapatos o accesorios"></b-img-lazy> 
    </div>
</template>
<script>
export default {
    name: 'Autocomplete',
    data: () => ({
      query: null,
      suggestions: [
            "Blazer",
            "Busos",
            "Camisas",
            "Camisetas",
            "Chaquetas",
            "Polos",
            "Blusas",
            "Cardigans",
            "Chalecos",
            "Enterizos",
            "Overoles",
            "Vestidos",
            "Adidas Originals",
            "Alpinestars",
            "Americanino",
            "Armani",
            "Big Star",
            "Chevignon",
            "Clover",
            "Derek",
            "Diesel",
            "Edc By Esprit",
            "Fila",
            "Fossil",
            "G-shock", 
            "Girbaud", 
            "Goorin", 
            "Jack & Jones",
            "Kenneth Cole", 
            "Le Coq Sportif", 
            "Michael Kors", 
            "New Balance", 
            "New Era", 
            "Nike", 
            "Oakley", 
            "Puma", 
            "Quiksilver", 
            "Ray-ban", 
            "Reebok", 
            "Roxy", 
            "Scout", 
            "Skechers", 
            "Superdry", 
            "Tiger Onitsuka", 
            "Typer", 
            "Unico", 
            "Unser", 
            "Vans"
        ]
    }),
    props: {
    },
    methods:{
        search(value){ console.log("Yija: " + value) },
        resize(){ 
            let inputAutocomplete = document.getElementById('search-autocomplete')
            let menuAutocomplete = document.querySelector("div.md-menu-content.md-autocomplete-box-content")
            menuAutocomplete.style.width = (inputAutocomplete.offsetWidth - 10) + "px"
            menuAutocomplete.style.maxWidth = (inputAutocomplete.offsetWidth - 10) + "px"
        }
    },
    mounted(){
        let vueTemp = this
        document.getElementById("search-autocomplete").addEventListener("keyup", function(event) {
            if (event.keyCode === 13) {
                vueTemp.search(this.value)
            }
        });
        if(window.innerWidth <= 769){
            document.querySelector("#search-icon").addEventListener("click", function() {
                let autocompleteCont = document.querySelector("#search-input .md-autocomplete.md-autocomplete-box")
                if(autocompleteCont.style.display=="block"){ autocompleteCont.style.display = "none" }
                else{ autocompleteCont.style.display = "block" }
            });
        }
    }
}
</script>
<style>
#search-input .md-autocomplete.md-autocomplete-box{
    margin: 0;
    background: #F1F4FF;
    box-shadow: none;
    border-radius: 20px;
}
#search-input .md-field{
    min-height: 0;
}
#search-input input#search-autocomplete{
    font-size: 14px;
}
#search-input img#search-icon{
    position: absolute;
    top: 0;
    right: 0;
    z-index: 18;
}
#search-input .md-field.md-inline.md-autocomplete-box .md-input-action {
    right: 40px;
}

@media (max-width:768px) {
    #search-input{
        position: unset !important;
    }
    #search-input .md-autocomplete.md-autocomplete-box{
        display: none;
        position: absolute;
        bottom: -3rem;
        left: 0;
        padding: 10px;
        background: #35495E;
        border-radius: 0;
        -webkit-transition: height .3s linear;
        transition: height .3s linear;
    }
    #search-input img#search-icon{
        position: relative;
    }
    #search-input img#search-icon{
        margin-left: 0;
    }
    #stirpe .md-menu-content{
        z-index: 10;
    }
    #search-input .md-autocomplete.md-autocomplete-box input{
        border-radius: 10px;
        background: #fff;
    }
    #search-input .md-field.md-inline.md-autocomplete-box .md-input-action {
        right: 8px;
    }
}
</style>