<template>
    <b-modal v-if="!hideModalNewsletter" id="modal-newsletter" size="lg" hide-footer centered body-class="row p-0 mx-0" @hide="hideModalTemporarily(15)">
        <b-col cols="5" class="d-none d-md-block newsletter-icon" style="background-image: url('/assets/img/bg-newsletter.png')">
            <b-img-lazy src="@/assets/icons/gift.svg" alt="Suscribete al Newsletter" />
        </b-col>
        <b-col cols="12" md="7" class="pb-2 pt-4">
            <h4 class="text-center">Suscrbete a nuestro <b>Newsletter</b></h4>
            <p class="text-center">Recibe ofertas exclusivas, historias nicas, nuevas colecciones, invitaciones y mucho ms.</p>
            
            <form>
            <b-row>
                <b-col cols="10" offset="1">
                    <md-field class="mb-4">
                        <label>Correo electrnico</label>
                        <md-input type="email" id="email"></md-input>
                    </md-field>
                </b-col>
                <b-col cols="10" offset="1">
                    <md-datepicker class="mb-4" id="birthday" :language="'es'">
                        <label>Fecha de cumpleaos</label>
                        <img src="@/assets/icons/calendar.svg" alt="Suscribete al Newsletter">
                    </md-datepicker>
                </b-col>
                <b-col cols="10" offset="1">
                    <b-form-checkbox-group class="mb-4" id="newsletter-gender">
                        <label>Me interesa la moda de...</label> <br>
                        <md-checkbox v-model="gender" value="m">Hombre</md-checkbox>
                        <md-checkbox v-model="gender" value="f">Mujer</md-checkbox>
                    </b-form-checkbox-group>
                </b-col>

                <b-col cols="10" offset="1" class="text-center">
                    <b-button variant="primary" class="px-5 py-2"><b>Suscribirme</b></b-button>
                </b-col>

                <b-col cols="10" offset="1" class="text-center mt-4">
                    <small>Inscribindote aceptas nuestra <b-link>poltica de privacidad</b-link>. Respetamos tu privacidad, te puedes de desuscribir en cualquier momento</small>
                </b-col>
            </b-row>
            </form>
        </b-col>
    </b-modal>
</template>
<script>
//TODO fetch para suscripcin a sendinblue
export default {
    name: 'ModalNewsletter',
    data: () => ({
        gender: [],
        hideModalNewsletter: false
    }),
    methods:{
        hideModalTemporarily(days){
            const now = new Date()
            const item = {
                expiry: now.getTime() + (days*24*60*60*1000)
            }
			localStorage.setItem('hideModalNewsletter', JSON.stringify(item))
        }
    },
    created(){
        const itemStr = localStorage.getItem('hideModalNewsletter')
        if (itemStr) {
            const item = JSON.parse(itemStr)
            const now = new Date()
            if (now.getTime() > item.expiry) {
                localStorage.removeItem('hideModalNewsletter')
            }
            else{
                this.hideModalNewsletter = true
            }
        }
    },
    mounted(){
        this.$bvModal.show('modal-newsletter')
    }
}
</script>
<style>
#modal-newsletter .modal-header{
    position: absolute;
    z-index: 1;
    right: 0;
    border-bottom: 0;
}
#modal-newsletter .modal-content{
    background: #EEEFF4;
}
#modal-newsletter h4{
    color: #1A1F2E;
    font-size: 26px;
}
#modal-newsletter .md-field.md-theme-default>.md-icon:after{
    background: transparent;
}
#modal-newsletter .newsletter-icon{
    background-size: cover;
    border-radius: 5px 0 0 5px;
}
#modal-newsletter .newsletter-icon img{
    position: absolute;
    height: 4rem;
    right: -2rem;
    top: calc(50% - 2rem);
}
#modal-newsletter .md-icon.md-icon-font.md-icon-image.md-date-icon.md-theme-default {
    display: none;
}
#modal-newsletter .md-field>.md-icon~label{
    margin-left: 0;
}
#modal-newsletter .md-field{
    background: #fff;
    padding: 10px 20px;
    border-radius: 8px;
    border: 2px solid #B6C1D3;
    min-height: auto;
}
#modal-newsletter .md-field label{
    top: 15px;
    left: 20px;
}
#modal-newsletter .md-field.md-focused label, #modal-newsletter .md-field.md-has-value label{
    top: -10px;
    left: 15px;
    background: #fff;
    padding: 0 10px;
}
#modal-newsletter #birthday img{
    position: absolute;
    right: 10px;
    top: 35%;
} 
#modal-newsletter .md-field .md-input-action{
    top: 20%;
    right: 30px;
}
#modal-newsletter .md-field>.md-icon~.md-input{
    margin-left: 0;
}
#modal-newsletter .md-field:after{
    height: 0;
}
#modal-newsletter .md-field::before{
    height: 1px;
}
#modal-newsletter .md-checkbox.md-theme-default.md-checked .md-checkbox-container{
    background-color: #0076FF;
    border-color: #0076FF;
}
#modal-newsletter  .md-checkbox.md-theme-default.md-checked .md-ripple{
    color: #0076FF;
}
#modal-newsletter .md-checkbox{
    margin: 0 16px 0;
}
#modal-newsletter #newsletter-gender{
    background: #fff;
    border-radius: 8px;
    padding: 0px 20px 5px;
    border: 2px solid #B6C1D3;
    margin-top: 5px;
}
#modal-newsletter #newsletter-gender > label{
    position: absolute;
    top: -5px;
    background: #fff;
    padding: 0 15px;
    font-size: 12px;
}
</style>