import Vue from 'vue'

import { MdButton, MdBadge, MdField, 
        MdDatepicker, MdCheckbox, MdAutocomplete, 
        MdMenu, MdMenuContent, MdMenuItem, MdListItem,
        MdList, MdDialog } from 'vue-material/dist/components'
import 'vue-material/dist/vue-material.min.css'
import 'vue-material/dist/theme/default.css'

Vue.use(MdButton)
Vue.use(MdBadge)
Vue.use(MdField)
Vue.use(MdCheckbox)

Vue.use(MdDatepicker)
Vue.use(MdDialog)

Vue.use(MdMenu)
Vue.use(MdList)
Vue.component('md-menu-item', MdMenuItem)
Vue.component('md-menu-content', MdMenuContent)
Vue.component('md-list-item', MdListItem)
Vue.use(MdAutocomplete)