<template>
    <div>
        <b-alert show variant="warning" class="mb-0 text-center">Compra ahora con tu cupo personal y <b>empieza a pagar en 60 das</b> <b-button>Solicitar crdito</b-button></b-alert>
        <div id="menu-small">
            <ul class="mb-0">
                <li>
                    <b-link><small>Encuentra una tienda</small></b-link>
                </li>
                <li>
                    <b-link href="#"><small>Visita <img src="@/assets/icons/logo-derek.svg" style="margin-top: -5px;" alt="Logo tienda Stirpe"></small></b-link>
                </li>
                <li>
                    <b-link><small>Centro de ayuda</small></b-link>
                </li> 
                <li>
                    <b-link><small> Rastrear pedido</small></b-link>
                </li>
            </ul>
        </div>
        <b-nav id="nav" :class="[fixedNav ? 'fixed-nav' : '']"> 
            <b-navbar-brand :to="{ name: 'Home' }"><img id="nav-logo" src="@/assets/img/logo-stirpe-negro.svg" alt="Logo tienda Stirpe"> </b-navbar-brand>
            <desktop-menu-item :key="index" v-for="(item, index) in menu" :menuItem="item" />
            <div class="d-flex flex-fill my-2 my-lg-0">
                <shortcut-menu />
            </div>
        </b-nav>
    </div> 
</template>

<script>
import DesktopMenuItem from '@/components/menu/DesktopMenuItem'
import ShortcutMenu from '@/components/menu/ShortcutMenu'

export default {
    name: 'DesktopMenu',   
    components: {
        DesktopMenuItem, ShortcutMenu
    },
    data(){
        return {
            fixedNav: false
        }
    },
    props: {
        menu:{
            type: Array,
            default: () => [],
            required: true
        }
    },
    methods: {
    },
    mounted(){
        window.document.onscroll = () => {
            let navBar = document.getElementById('nav');
            if(window.scrollY > navBar.offsetTop){
                this.fixedNav = true;
                } else {
                this.fixedNav = false;
            }
        }
    }
}
</script>

<style>
#nav{
  position: relative;
  z-index: 99;
}
#nav #nav-logo{
    padding-top: 12px;
}
#nav a:hover{
  text-decoration: none;
}
#nav a.router-link-exact-active {
  font-weight: bold;
}
#nav .nav-departament a:after{
    content: ' ';
    position: absolute;
    bottom: 5px;
    left: 1rem;
    height: 2px;
    width: 0%;
    background: #0076FF;
    -webkit-transition: width .1s linear;
    transition: width .1s linear;
}
#nav .nav-departament a.router-link-exact-active:after, #nav .nav-departament a:hover:after{
    width: calc(100% - 2rem);
}
#nav .navbar-brand{
    margin-left: 30px;
}
#nav-container .fixed-nav{
    position: fixed;
    width: 100%;
    top: 0;
    background: #fff;
}
#nav-container .alert{
    padding: .5rem 1.25rem;
    border-radius: 0;
}
#nav-container .alert.alert-warning{
    background: #F2B948;
    color: #fff;
}
#nav-container .alert .btn{
    border-radius: 18px;
    border: none;
    font-size: 14px;
    margin-top: -3px;
}
#nav-container .alert.alert-warning .btn{
    background: #fff;
    color: #F2B948;
}
#menu-small{
    display: block;
    text-align: right;
    padding: 10px;
    background: #F1F4FF;
}
#menu-small ul li{
    display: inline-block;
    padding: 0 15px;
}
#menu-small ul li::before{
    display: inline-block;
    content: '...';
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    margin-left: -15px;
    margin-right: 5px;
}
#menu-small ul li a{
    color: #000;
}
</style>